button {
  width: 200px;
  height: 25px;
}
table {
  width: 80%;
  border-bottom: 2px solid #444444;
  border-collapse: collapse;
}

th{
  background-color: #e6e6e6;
  border-top: 2px solid #444444;
  border-bottom: 2px solid #444444;
  padding: 5px 10px;
  text-align: center;
  font-weight: bold;
}

td {
  border-bottom: 1px solid #444444;
  padding: 5px 10px;
}

#contents {
  font-family: "맑은 고딕";
  font-size: 10pt;
}

.content-text {
  margin: 20px 0 20px 0;
}
